import Interceptor from './core/interceptor';
 import Request from './index';
 import TokenApi from '../.././../api/request';
 import store from '../../../store'

 export const globalInterceptor = {
 	request: new Interceptor(),
 	response: new Interceptor()
 };

 /**
  * 
  *  
  * `content-type`  application/json
  * `header` `content-type`    catch
  */

 export const config = {
 	baseURL: store.state.$state_url,
 	timeout: 99999,
 	header: {
		'satoken':uni.getStorageSync('token')?uni.getStorageSync('token'):store.state.token,
 		'Content-Type': 'application/json'
 	}
 };

 /**
  *  , 
  * : token
  *
  * `return config` 
  * `return false` catch
  * `return Promise.reject('xxxxx')` , catch
  *
  * @param {Object} config 
  */
 globalInterceptor.request.use(
 	(config) => {
 		console.log('is global request interceptor');
 		getToken() && (config.header.satoken = getToken());
 		return config;
 	},
 	(err) => {
 		console.error('is global fail request interceptor: ', err);
 		return false;
 	}
 );

 /**
  *  , 
  * : 
  *
  * `return res` 
  * `return false` catch
  * `return Promise.reject('xxxxx')` , catch
  *
  * @param {Object} res 
  * @param {Object} config 
  * @return {Object|Boolean|Promise<reject>}
  */
 globalInterceptor.response.use(
 	async (res, config) => {
 			////////////////////////////////////////
 			//  demo codehttp   //
 			////////////////////////////////////////

 			console.log('is global response interceptor');


 			//  `request().download()` 
 			if (typeof res.tempFilePath !== 'undefined') {
 				return res;
 			}

 			const {
 				data,
 				data: {
 					code
 				}
 			} = res;
 			try {
 				return await handleCode({
 					data,
 					code,
 					config
 				});
 			} catch (err) {
 				return Promise.reject(err);
 			}
 		},
 		(err, config) => {
 			console.error('is global response fail interceptor');
 			console.error('err: ', err);
 			console.error('config: ', config);
 			// showToast('...');
 			return Promise.reject(err);

 			// return false;
 		}
 );



 /**
  *  `localStorage`  `token`
  * @return {string} token
  */
 function getToken() {
 	return uni.getStorageSync('token');
 }

 /**
  *  `token`   `localStorage`
  * @param {string} token token
  * @return {void}
  */
 function saveToken(token) {
 	uni.setStorageSync('P_token', 'Bearer ' + token);
 }

 /**
  *  http
  * @param {object} o
  * @param {object} o.data 
  * @param {object} o.config config
  * @param {string|number} o.code http
  * @return {object|Promise<reject>}
  */

 var count = 0;

 function handleCode({
 	data,
 	code,
 	config
 }) {
	 console.log({
		 data,
		 code,
		 config
	 });
	 return data;
 	const STATUS = {
 		'0'() {
 			return data;
 		},
 		'7'() {
 			showToast(data.msg);
 			if (data.data.reload) {
 				uni.reLaunch({
 					url: '../mine/login'
 				})
 			}
 			return data;
 		},
 		'200'() {
 			return data;
 		},
 		'201'() {
 			return data;
 		},
 		'400'() {
 			return data;
 		},
 		'404'() {
 			return data;
 			// return Promise.reject({ code, code.msg });
 		},
 		'204'() {
 			return data;
 		},
 		'403'() {
 			showToast("");
 			return data;
 			// return Promise.reject({ code, code.msg });
 		},
 		'500'() {
 			return data;
 		},
 		'502'() {
			uni.removeStorageSync('hasLogin')
			uni.removeStorageSync('username') 
			uni.redirectTo({
				url:'/pages/index/index.vue'
			})
 			return data;
 		}
 	};
   if (!STATUS[code]) {
   	uni.removeStorageSync('hasLogin')
   	uni.removeStorageSync('username') 
   	uni.redirectTo({
   		url:'/pages/index/index.vue'
   	})
   }
 	return STATUS[code] ? STATUS[code]() : Promise.reject(data, config); //  `fail`
 }

 // 
 function showToast(data) {
 	uni.showToast({
 		title: JSON.stringify(data),
 		icon: 'none',
 		duration: 5000
 	});
 }
