 const COS=require("./cos-wx-sdk-v5.js")

 //
 function uploadFile({filename,filePath,data}){
 
/* 	const Bucket="";// BucketName-APPID
	const Region="";// */
	// COS SDK 
	// SECRETID  SECRETKEY https://console.cloud.tencent.com/cam/capi 
	const cos = new COS({
		getAuthorization: function (options, callback) {
		  callback({
		    TmpSecretId: data.credentials.tmpSecretId,
		    TmpSecretKey: data.credentials.tmpSecretKey,
		    XCosSecurityToken: data.credentials.sessionToken,
		    StartTime: data.startTime,
		    ExpiredTime: data.expiredTime,
		    expiration: data.expiration
		  })
		}
		// TmpSecretId: data.credentials.tmpSecretId,
		// TmpSecretKey: data.credentials.tmpSecretKey,
		// XCosSecurityToken: data.credentials.sessionToken,
		// StartTime: data.startTime,
		// ExpiredTime: data.expiredTime,
		// expiration: data.expiration,
	});
	// //
	// cos.putBucket({
	//   Bucket:data.bucket,
	//   Region:data.region,
	// }, function (err, data) {
	//   console.log(err || data);
	// });
	
	// //
	// cos.getService(function (err, data) {
	// 	console.log("[]")
	//   console.log(data && data.Buckets);
	// });
 
	return new Promise((resolve,reject)=>{
 		cos.postObject({
 		    Bucket: data.bucket,
 		    Region: data.region,
 		    Key: 'wxFile/' + filename,
 		    FilePath: filePath,
 		    onProgress: function (info) {
 		        // console.log("[cos.postObject-seccess]",JSON.stringify(info));
 		    }      
 		}, 
 		function (err, data) {
 		    // console.log("[cos.postObject-err]",err || data);
 			resolve(data.headers.location)
 		})
 	})
 }
 
   export default uploadFile