// 
class Interceptor {
    constructor() {
        this.handlers = [];
    }

    /**
     *  
     * @param {Function} fulfilled Promise.resolve
     * @param {Function} rejected  Promise.reject
     * @return {Number} id
     */
    use (fulfilled, rejected) {
        this.handlers.push({
            fulfilled,
            rejected
        });

        return this.handlers.length - 1;
    }

    /**
     *  
     * @param {Number} id id
     */
    eject (id) {
        this.handlers[id] && (this.handlers[id] = null);
    }

    /**
     * 
     * @param {Function} fn 
     */
    forEach (fn) {
        this.handlers.forEach(item => {
            item && fn(item);
        });
    }
}

export default Interceptor;